<template>
 <div>
  <cms-breadcrumb to="/shopAccount/accountList/list"  >
    <li>{{$route.name}}</li>
  </cms-breadcrumb>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>店铺账户明细变更记录</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>资金类型 </label>
        <el-select v-model="queryParams.capitalChangeType" @change="query">
          <el-option label="全部" value=" "></el-option>
          <el-option label="账户金额" :value="1"></el-option>
          <el-option label="保证金" :value="2"></el-option>
          <el-option label="冻结金额" :value="3"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>资金变更方式</label>
        <el-select v-model="queryParams.calculatorType" @change="query">
          <el-option label="全部" value=" "></el-option>
          <el-option label="增加" :value="1"></el-option>
          <el-option label="减少" :value="2"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="storeName" label="店铺名称" min-width='140' show-overflow-tooltip class-name="text-overflow"></el-table-column>
      <el-table-column prop="userName" label="商家账号" min-width="140"></el-table-column>
      <el-table-column prop="capitalChangeType" label="资金类型" min-width="110">
        <div slot-scope="scope">
          <span class="error" v-if="scope.row.capitalChangeType === 1">账户金额</span>
          <span class="success" v-else-if="scope.row.capitalChangeType === 2">保证金</span>
          <span class="error" v-else-if="scope.row.capitalChangeType === 3">冻结金额</span>
        </div>
      </el-table-column>
      <el-table-column prop="createTime" label="调整时间" min-width="170">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column prop="remark" label="备注" min-width="190" >
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.remark"> {{scope.row.remark}}</div>
        </template>
      </el-table-column>
      <!-- :class-name="money>0?red:green" -->
      <el-table-column  prop="money" label="调整金额" min-width="110" >
        <template slot-scope="scope">
           <!-- :class='[scope.row.calculatorType===1?"add-mon":"dec-mon"]' -->
          <span :class='[scope.row.calculatorType===1?"green":"red"]'>{{scope.row.calculatorType===1?'+':'-'}}{{$cms.float(scope.row.money)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="beforeMoney" label="调整前的金额" min-width="110">
        <template slot-scope="scope">
          {{$cms.float(scope.row.beforeMoney)}}
        </template>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
 </div>
</template>
<script>
import list from '@/mixins/list'
import { detailPage } from '@/api/shop/shopAccount.js'
var defaultData = {
  storeId: '',
  size: 10,
  page: 1
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {},
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(detailPage, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.queryParams.storeId = this.$route.query.id
    this.getTableData(detailPage, this.queryParams)
  }
}
</script>

<style>
.red{
  color: red
}
.green{
  color: green
}
</style>



